@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro:regular,bold,italic&subset=latin,latin-ext);
.loading{position:fixed;top:50%;left:50%}

.dashboard-container{width:80%;padding:30px;margin:0 auto;background:rgba(128,128,128,0.08)}.chart{padding:30px}.project-button-group{text-align:right}.project-button{margin:0 5px}

.image-preview{text-align:center}.image-preview img{background-color:black;max-width:560px;min-height:350px;max-height:450px;transition:0.2s;margin-top:20px;object-fit:cover}

audio:focus{outline:none}.audio-preview{text-align:center}.audio-preview audio{margin-top:20px}

.image-task-container{height:600px}.task-description{text-align:center}.flex-item{text-align:center;min-width:90px}.req-error{margin-top:16px}.ml-suggestion{margin:5px;cursor:pointer}

.project-title{width:60%;margin:0 auto;margin-top:20px}.main-form-container{width:80%;margin:0 auto}.form-button{margin:5px}.task-counter{cursor:default}.completed-message{width:80%;margin:0 auto}

.task-creation{width:100%;height:100%;display:grid;grid-template-columns:1fr 1fr}.task-creation .editor{box-sizing:border-box;height:100%;grid-column:0 1;border-right:#D6D6D6 5px solid}.task-creation .preview{box-sizing:border-box;border-left:#D6D6D6 5px solid;height:100%;grid-column:1 2}.task-creation .title{width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;box-sizing:border-box;font-size:20px;font-family:"Ubuntu",sans-serif;color:#9d9d9d;font-weight:700;margin-top:10px;margin-left:50%;transform:translateX(-50%)}.btn-next{position:absolute;bottom:50px;right:50px}

.data-type{box-sizing:border-box;width:80%;padding:10px 20px;border:1px solid #D6D6D6;border-radius:5px}.setting-component{width:80%;margin:0 auto;display:flex;flex-direction:column;justify-content:center;align-items:center}.setting-component .input-setting-row{width:100%;display:flex;flex-direction:row;align-items:center;margin-top:10px}.setting-component .input-setting-row input,.setting-component .input-setting-row textarea,.setting-component .input-setting-row .ant{width:100%;margin-left:10px;margin:0 10px 0 10px;border-radius:5px !important}.setting-component .input-setting-row .ant-select{width:100%;margin-left:10px}.setting-component .input-setting-row .ant-select-selector{border-radius:5px !important}.setting-component .input-setting-row-sp{display:inline;box-sizing:border-box}.setting-component .input-setting-row-sp button{margin-right:5px;margin-top:5px}.setting-component .setting-sct{background:#fbfbfb;width:100%;max-width:calc(350px + 26.8px);margin:20px auto 0px auto;border:1px solid #fbfbfb;border-radius:5px;padding:10px}.project-name{width:calc(90% + 26.8px);max-width:calc(350px + 26.8px);border-radius:5px}

.dropdown-container{margin:20px auto 0px auto;width:100%;display:flex;flex-direction:row;align-items:center;justify-content:center}.dropdown-container .not-dropdown{border:1px solid #D6D6D6;border-radius:5px;padding:10px;padding-top:5px;padding-bottom:5px}.dropdown-container .dropdown{width:90%;max-width:350px;box-sizing:border-box}.dropdown-container .dropdown .ant-select-selector{border-radius:5px !important}.dropdown-container .icon{margin-left:10px;cursor:pointer;font-size:1.2em}.dropdown-container .icon svg{transition:.5s;transform:rotateZ(0deg)}.dropdown-container .rotated-icon svg{transform:rotateZ(90deg)}

.img-preview{margin:0 auto;display:flex;justify-content:center}.img-preview img{background-color:black;max-width:600px;max-height:600px;min-width:100px;min-height:100px;transition:.2s;margin-top:20px;width:80%;object-fit:cover}

audio:focus{outline:none}.audio-preview{text-align:center}.audio-preview audio{margin-top:20px}






.input-area-preview{width:80%;display:flex;flex-direction:column;margin:0 auto;justify-content:center;margin-top:20px}

.main-chart{width:60%;margin:0 auto}.projects-table{width:80%;margin:0 auto}.tabs-container{margin-top:20px}


.form-item{margin-bottom:10px}.submit-button{text-align:center;width:100%;margin-top:20px;margin-bottom:0px}.auth-form{height:100vh}

.upload-files-container{margin:0 auto;width:70%;height:100%}.upload-files-container .upload-files:hover{border:1px dashed #00a6ff;box-shadow:0px 0px 5px #6dcff669}.upload-files-container .upload-files:hover .icon{color:#00a6ff}.upload-files-container .upload-files-log{width:60%;max-width:500px;margin:0 auto}.upload-files-container .upload-files-log .log-suc{width:100%;padding:5px 0;color:green}.upload-files-container .upload-files{transition:0.5s;max-width:500px;box-sizing:border-box;padding:20px;margin:0 auto;width:60%;position:relative;cursor:pointer;border:1px solid #d6d6d6;border-radius:10px;text-align:center}.upload-files-container .upload-files .icon{transition:0.5s;font-size:5em;color:#d6d6d6}.upload-files-container .upload-files input{width:100%;height:100%;top:0;left:0;position:absolute;opacity:0;cursor:pointer}.feedback{margin:10px auto;text-align:center;color:red}

@media screen{@font-face{font-family:"Ubuntu";font-style:italic;font-weight:normal;src:local("Ubuntu Italic"),local("Ubuntu-Italic"),url("http://themes.googleusercontent.com/static/fonts/ubuntu/v4/kbP_6ONYVgE-bLa9ZRbvvvesZW2xOQ-xsNqO47m55DA.woff") format("woff")}}@media screen{@font-face{font-family:"Ubuntu";font-style:normal;font-weight:normal;src:local("Ubuntu"),url("http://themes.googleusercontent.com/static/fonts/ubuntu/v4/_xyN3apAT_yRRDeqB3sPRg.woff") format("woff")}}@media screen{@font-face{font-family:"Ubuntu";font-style:normal;font-weight:bold;src:local("Ubuntu Bold"),local("Ubuntu-Bold"),url("http://themes.googleusercontent.com/static/fonts/ubuntu/v4/0ihfXUL2emPh0ROJezvraD8E0i7KZn-EPnyo3HZu7kw.woff") format("woff")}}.trigger{padding:10px 30px;line-height:10px;cursor:pointer;color:white}.content-layout{background:#fff;padding-left:50px;padding-top:20px}.sidebar-textstyle{min-height:100vh;height:100%;background:rgba(0,0,0,0.85);font-family:"Open Sans";font-size:16px;color:#fff;padding-bottom:450px;font-family:"Ubuntu";font-style:normal;font-weight:normal}.link a,.link a:hover,.link a:visited,.link a:active{color:inherit;text-decoration:none}

.landing{transition:2s;width:100vw;height:100vh;margin-top:-50px;display:flex;align-items:center;justify-content:center;flex-direction:row}.landing .left-segment{text-align:justify;width:30%;max-width:500px}.landing .left-segment p{margin:0}.landing .left-segment .title{font-size:3em;margin-top:-0.5em}.landing .left-segment .text{margin:10px 0 15px 0}.landing .right-segment{width:40%;max-width:500px}.landing-start{transform:translateY(50px);opacity:0}.landing-stop{transform:translateY(0px);opacity:1}

.review-container{width:70%;margin:0 auto;padding:30px;background:rgba(128,128,128,0.08)}.review-content .ant-list-item-meta-content{width:100%;margin-left:75px}.review-list .ant-spin-nested-loading{min-height:800px}.review-list .ant-list-pagination{margin-top:0px}.review-data{text-align:center}

.profile-container{width:80%;padding:30px;margin:0 auto;background:rgba(128,128,128,0.08)}

*{padding:0;margin:0;box-sizing:border-box}body,html,#root{height:100%;width:100%;font-family:'Open Sans', sans-serif}.title-line{font-family:"Ubuntu",sans-serif}

